<template>
  <v-layout column id="education">
    <v-responsive color="primary" dark class="about-me">
      <v-container fill-height>
        <v-layout align-center>
          <v-flex text-xs-center>
            <span class="display-2 heading-title">EDUCATION</span>
          </v-flex>
        </v-layout>
      </v-container>
    </v-responsive>
    <v-container>
      <v-layout row wrap justify-center align-center>
        <v-card class="edu-card">
          <v-tabs v-model="tab" background-color="transparent" color="basil">
            <v-tab>Educations</v-tab>
            <v-tab>Cetifications</v-tab>
            <v-tab-item>
              <ul v-for="(Education, index) in Educations" :key="index">
                <li>
                  <v-card-text class="edu-time">
                    <b>
                      <i>{{ Education.Time }}</i>
                    </b>
                  </v-card-text>
                </li>
                <li>
                  <v-card-text>
                    <b>University:</b>
                    {{ Education.University }}
                  </v-card-text>
                </li>
                <li>
                  <v-card-text>
                    <b>Specialize</b>
                    {{ Education.Specialize }}
                  </v-card-text>
                </li>
                <li>
                  <v-card-text>
                    <b>GPA:</b>
                    {{ Education.GPA }}
                  </v-card-text>
                </li>
              </ul>
            </v-tab-item>
            <v-tab-item>
