                ></v-img>
                <v-card-text
                  ><a :href="Certification.link">{{
                    Certification.name
                  }}</a></v-card-text
                >
              </v-card>
            </v-tab-item>
          </v-tabs>
        </v-card>
      </v-layout>
    </v-container>
  </v-layout>
