<script>
export default {
  data() {
    return {
      tab: null,
      Educations: [
        {
          Time: "2017-2021",
          University: "HCM University of Science",
          Specialize: "Infomation Technology(Infomation System)",
          GPA: 7.5
        },
        {
          Time: "2017-2021",
          University: "HCM University of Science",
          Specialize: "Infomation Technology(Infomation System)",
          GPA: 7.5
        }
      ],
      Certifications: [
        {
          name: "APIs and Microservices",
          image: require("@/assets/apis.jpeg"),
          link:
            "https://www.freecodecamp.org/certification/voducdan/apis-and-microservices"
        },
        {
          name: "JavaScript Algorithms and Data Structures",
          image: require("@/assets/js.jpeg"),
          link:
            "https://www.freecodecamp.org/certification/voducdan/javascript-algorithms-and-data-structures"
        },
        {
          name: "Responsive Web Design",
          image: require("@/assets/RPW.jpeg"),
          link:
            "https://www.freecodecamp.org/certification/voducdan/responsive-web-design"
        }
      ]
    };
  },
  methods: {}
};
