<style lang="scss">
$ExtraSmall: "(max-width: 599px)";
$Small: "(min-width: 600px) and (max-width: 959px)";
$Medium: "(min-width: 960px) and (max-width: 1263px)";
$Large: "(min-width: 1264px) and (max-width: 1903px)";
@mixin ExtraSmall {
  @media #{$ExtraSmall} {
    @content;
  }
}
.edu-card {
  width: 100%;
  .v-window {
    .v-window__container {
      .v-window-item {
        ul {
          list-style: none;
          @include ExtraSmall{
