          }
          li {
            .v-card__text {
              padding: 8px 16px !important;
              width: auto !important;
              font-size: 1.001rem;
            }
            .v-card__text.edu-time {
              font-size: 1.1rem;
            }
          }
        }
      }
      .v-window-item--active {
        display: flex;
        justify-content: flex-start;
        flex-wrap: wrap;
        ul {
          width: 30%;
          padding: 30px 0 0 0;
        }
        .v-card {
          margin: 20px 15px;
          width: 23%;
          .v-card__text {
            padding: 5px !important;
            text-align: center;
            a {
              color: #ad7676;
            }
          }
        }
      }
    }
  }
}
</style>
