<template>
  <v-layout wrap class="toggle" fill-height>
    <v-btn light @click.stop="drawerNav">
      <i class="fas fa-bars"></i>
    </v-btn>
    <v-navigation-drawer :mini-variant="mini" absolute dark>
      <v-list class="pa-1">
        <v-list-item avatar tag="div">
          <v-list-item-avatar>
            <img src="../../assets/avatar.jpg" />
          </v-list-item-avatar>

          <v-list-item-content>
            <v-list-item-title>
              <a href="/cv" style="text-decoration:none; color:white"
                >Vo Duc Dan</a
              >
            </v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>

      <v-list class="pt-0" dense>
        <v-divider light></v-divider>

        <v-list-item v-for="item in items" :key="item.title">
          <v-list-item-action>
            <span v-html="item.icon"></span>
          </v-list-item-action>
          <v-list-item-content>
            <v-list-item-title>
              <router-link :to="item.path" class="navitem">
                {{ item.title }}
              </router-link>
            </v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>
  </v-layout>
</template>
<script>
export default {
  data() {
    return {
      drawer: null,
      items: [
        {
          title: "About",
          path: "/Resume#about",
          icon: '<i class="fas fa-user"></i>'
        },
        {
          title: "Skill",
          path: "/Resume#skill",
          icon: '<i class="fas fa-signal"></i>'
        },
        {
          title: "Projects",
          path: "/Resume#projects",
          icon: '<i class="fas fa-chart-bar"></i>'
        },
        {
          title: "Education",
          path: "/Resume#education",
          icon: '<i class="fas fa-book"></i>'
        }
      ],
      mini: false,
      right: null
    };
  },
  methods: {
    drawerNav() {
      this.drawer = !this.drawer;
      const toggle = document.querySelector(".toggle");
      if (this.drawer === true) {
        toggle.classList.remove("unDrawer");
        toggle.classList.add("drawer");
      } else {
        toggle.classList.remove("drawer");
        toggle.classList.add("unDrawer");
      }
    }
  }
};
</script>
<style lang="scss">
.about-me {
  height: 100px !important;
}
.drawer {
  animation: drawer 0.2s forwards;
}
.unDrawer {
  animation: undrawer 0.2s forwards;
}
@-webkit-keyframes drawer {
  from {
    transform: translateX(-250px);
  }
  to {
    transform: translateX(0);
  }
}
@-webkit-keyframes undrawer {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(-250px);
  }
}
.navitem {
  color: white;
  text-decoration: none;
  font-weight: 500;
}
.toggle {
  transform: translateX(-250px);
  height: 100vh;
  position: fixed;
  width: 300px !important;
  z-index: 1;
  .v-overlay {
    display: none !important;
  }
  button {
    height: 40px;
    min-width: 40px !important;
    position: absolute;
    top: 0;
    left: 250px;
  }
  .v-navigation-drawer {
    transform: translateX(0) !important;
  }
}
</style>
